<template>
  <div class="seller-order-detail">
    <backer-header title="订单详情" />
    <scroll ref="scroll" :class="{return: order.return_status===1}" clickable>
      <div class="order">
        <div class="order-no">
          <p>订单编号：{{ order.order_no }}</p>
          <span>{{ order.status === 4 ? refundStatus[order.return_status] : (order.status === 1 && order.delivery_status) ? '已发货' : status[order.status] }}</span>
        </div>
        <order-item class="item" v-for="good in goods" :key="good.id" :size-drop="false" :goods="good" price-show />
      </div>
      <div class="address">
        <van-icon class="location-icon" name="location-o" />
        <div class="info">
          <p>{{ order.receiver_name }} <span>{{ order.receiver_mobile }}</span></p>
          <span>{{ order.receiver_address }}</span>
        </div>
      </div>
      <div class="connect" @click="chat">
        <img src="./maijia@2x.png" alt="">
        <p>联系买家</p>
      </div>
      <div class="order-intro">
        <p v-if="order.remark"><span>订单备注</span>{{ order.remark }}</p>
        <p><span>订单编号</span>{{ order.order_no }}</p>
        <p v-if="order.status !== 4"><span>创建时间</span>{{ order.create_time }}</p>
        <p v-if="order.status === 4"><span>申请时间</span>{{ order.return_info.create_time }}</p>
        <p v-if="order.status === 4"><span>退款金额</span>{{ order.return_info.amount }}</p>
        <p v-if="order.pay_no && order.status !== 4"><span>支付方式</span>{{ order.pay_channel === 1 ? '微信支付' : '支付宝支付' }}</p>
        <p v-if="order.pay_time && order.status !== 4"><span>支付时间</span>{{ order.pay_time }}</p>
        <p v-if="order.express_no && order.status !== 4"><span>物流单号</span>{{ order.express_no }}</p>
        <p v-if="order.return_info && order.return_info.express_no && order.status === 4"><span>物流单号</span>{{ order.return_info.express_no }}</p>
        <p v-if="order.return_info && order.return_info.refund_trade_no && order.status === 4"><span>退款单号</span>{{ order.return_info.refund_trade_no }}</p>
      </div>
      <div @click="delivery" class="bottom-btn" v-if="order.status === 1 && order.delivery_status === 0">立即发货</div>
    </scroll>
    <div class="return-btn" v-if="order.return_status===1">
      <button @click="closeReturn">拒绝退款</button>
      <button @click="refund">接受退款</button>
    </div>
    <div @click="returnMoney" v-if="order.return_info && order.return_info.express_no && order.return_status === 2" style="position: fixed;" class="bottom-btn">已收货，立即退款</div>
  </div>
</template>

<script>
import BackerHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import OrderItem from '../../components/order-item/order-item'
import { post } from '../../common/js/http'

export default {
  name: 'seller-order-detail',
  components: {
    BackerHeader,
    Scroll,
    OrderItem
  },
  data() {
    return {
      order: {
        return_info: {}
      },
      goods: [],
      refundStatus: ['', '待审核', '已同意', '已拒绝', '已完成'],
      status: ['待付款', '待发货', '已发货', '已完成', '退款']
    }
  },
  mounted() {
    const { type } = this.$route.query
    if (type !== '4') {
      this.getDetail()
    } else {
      this.getRefundDetail()
    }
  },
  methods: {
    chat() {
      this.$jsCallNative('chat', {
        uid: this.order.user.id + '',
        nick_name: this.order.user.nick_name
      })
    },
    refund() {
      this.$router.push({
        name: 'seller-refunds',
        params: {
          order: this.order,
          goods: this.goods
        }
      })
    },
    delivery() {
      this.$router.push({
        name: 'delivery',
        params: {
          goods: this.goods,
          orderId: this.order.id
        }
      })
    },
    getRefundDetail() {
      const loading = this.$skeLoading('.seller-order-detail', '0')
      post('/shop/order/returnOrderInfo', {
        ordergoodsid: this.$route.params.id
      })
        .then(({ data: { data } }) => {
          this.goods = [{
            ...data.ordergoods,
            goodsId: data.ordergoods.id,
            title: data.ordergoods.goods.title,
            goods: null,
            size: data.ordergoods.color + (data.ordergoods.size || ''),
            cover: data.ordergoods.goods.thumb_urls.split(',')[0]
          }]
          this.order = {
            ...data.ordergoods.suborder,
            shop: data.ordergoods.shop,
            user: data.ordergoods.user,
            status: 4,
            return_status: data.return_info.status,
            return_info: data.return_info
          }
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
          loading.close()
        })
    },
    getDetail() {
      const loading = this.$skeLoading('.seller-order-detail')
      post('/shop/order/getOrderInfo', {
        suborderid: this.$route.params.id
      })
        .then(({ data: { data } }) => {
          this.order = data.order
          this.goods = data.goods.map(goods => {
            return {
              ...goods,
              title: goods.goods.title,
              goods: null,
              size: goods.color + (goods.size || ''),
              cover: goods.goods.thumb_urls.split(',')[0]
            }
          })
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
          loading.close()
        })
    },
    closeReturn() {
      const loading = this.$toast.loading()
      post('/shop/order/operateReturn', {
        returnid: this.order.return_info.id,
        operate: 2
      })
        .then(() => {
          loading.close()
          this.getRefundDetail()
        })
        .catch(() => {
          loading.close()
        })
    },
    returnMoney() {
      const loading = this.$toast.loading()
      post('/shop/order/shopReceiveReturn', {
        returnid: this.order.return_info.id
      })
        .then(() => {
          loading.close()
          this.getRefundDetail()
        })
        .catch(() => {
          loading.close()
        })
    }
  }
}
</script>

